import mpvueCityPicker from "@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue"
import {mapState,mapActions} from "vuex"
export default {
	components:{
		mpvueCityPicker
	},
	data() {
		return {
			index:"",
			themeColor: '#007AFF',
			isedit:false,
			pickerText:"",
			cityPickerValueDefault: [0, 0, 1],
			from:{
				name: "",
				phone: "",
				detailPath: "",
				isDefault: false,
				path:"",
			}
		}
	},
	methods: {
		...mapActions(['updatePathAction','createPathAction']),
		// 保存
		sum(){
			if(this.isedit){
				let data = {
					index:this.index,item:this.from
				}
				data['callback']=()=>{
					uni.navigateBack({
						delta:1
					})
					uni.showToast({
						title:"修改成功"
					})
				}
				this.updatePathAction(data)
				return;
			}
			this.createPathAction(this.from)
			uni.showToast({
				title:"创建成功"
			})
			uni.navigateBack({
				delta:1
			})
			
		},
		pickerCancel() {
			this.showPicker = false;
			this._$emit('onCancel');
		},
		// 提交确定
		onConfirm(e) {
			this.from.path =e.label
			this.pickerText = e.label
			
		},
		// 显示三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
	},
	// 监听返回键先关闭三级联动
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
	  	this.$refs.mpvueCityPicker.pickerCancel();
	    return true;
	  }
	},
	onLoad(e) {
		if(e.data){
			this.isedit = true
			this.from = JSON.parse(e.data).item
			this.index =  JSON.parse(e.data).index
			uni.setNavigationBarTitle({
				title:"修改收货地址"
			})
		}
		uni.setNavigationBarTitle({
			title:"增加收货地址"
		})
	},
	// 页面卸载
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
	
}
